.filtrationBlock {
  display: flex;
  padding: 8px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 304px;
  height: 32px;
  background-color: var(--white);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.04);
  border-radius: 24px;
}

.filtButton {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--grey2);
  width: 64px;
  height: 32px;
  font-family: Montserrat;
  font-size: 14px;
  background: var(--transparent);
  border-radius: 16px;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 0.3s;
}

.filtButton:hover {
  color: var(--white);
  background: linear-gradient(201.66deg, #b380fd 12.94%, #6347f5 78.23%);
}

.active {
  color: var(--white);
  background: linear-gradient(201.66deg, #b380fd 12.94%, #6347f5 78.23%);
}
